.backdrop-team {
  opacity: 1;
  width: 100%;
  height: 100%;
  background: #00000040;
  padding-top: 20px;
  padding-bottom: 20px;
  transition: opacity .5s, visibility .5s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop-team.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop-team.is-hidden .modal-team {
  transform: translate(-50%, -50%)scale(.4);
}

.modal-team {
  -ms-overflow-style: none;
  scrollbar-width: none;
  background: #fff;
  border-radius: 5px;
  padding: 30px;
  transition: transform .5s;
  position: relative;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

