.content {
  position: relative;
  width: 100%;

  @media screen and (max-width: 767px) {
    padding: 20px 0 40px;
  }

  @media screen and (min-width: 768px) {
    padding: 60px 0;
  }
}

.gallery {
  display: grid;
  justify-content: center;
  margin: 0 auto;

  @media screen and (max-width: 767px) {
    grid-template-columns: 280px;
    grid-auto-rows: 443px;
    gap: 20px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    grid-template-columns: 336px 336px;
    grid-auto-rows: 495px;
    grid-gap: 32px;
  }

  @media screen and (min-width: 1280px) {
    grid-template-columns: 395px 395px 395px;
    grid-auto-rows: 634px;
    grid-gap: 32px 16px;
  }

  &__card {
    cursor: pointer;

    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    border-radius: 5px;
    transition: all 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

    @media screen and (max-width: 767px) {
      font-size: 12px;
      line-height: 1.33;
    }

    @media screen and (min-width: 768px) and (max-width: 1279px) {
      font-size: 12px;
      line-height: 1.33;
    }

    @media screen and (min-width: 1280px) {
      height: 634px;
      font-size: 20px;
      line-height: 1.2;
    }
    &:hover {
      transform: scale(1.03);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    body.dark__theme & {
      box-shadow: 0 14px 28px rgba(255, 255, 255, 0.25),
        0 10px 10px rgba(255, 255, 255, 0.22);
    }
    }
  }

  &__card:hover,
  &__card:focus{
    transform: scale(1.03);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  }

  &__image {
    width: 100%;
    height: 91%;
    object-fit: cover;
    border-radius: 5px;

    @media screen and (max-width: 767px) {
      margin-bottom: 10px;
    }

    @media screen and (min-width: 768px) and (max-width: 1279px) {
      margin-bottom: 8px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 12px;
    }
  }

  &__data {
    display: inherit;
    flex-direction: inherit;
  }

  &__name {
    text-transform: uppercase;

    body.dark__theme & {
      color: $white-color;
    }
  }

  &__stats {
    display: inherit;
    flex-direction: row;
    align-items: self-start;
    flex-wrap: wrap;
    color: $main--accent;
  }

  &__rating {
    // display: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 20px;
    margin-left: 8px;
    border-radius: 5px;
    color: $main--background;
    background-color: $main--accent;
    font-size: 12px;
    line-height: 1.17;
  }
}

.template {
  border-radius: 5px;
  width: 100%;
  height: 100%;
  background: linear-gradient(
      60deg,
      transparent 0 40%,
      #ffffff77,
      transparent 60% 100%
    ),
    #aaa;
  background-size: 130%;
  background-repeat: no-repeat;
  background-position: -450px 0;
  animation: loading 1000ms infinite;
}

@keyframes loading {
  100% {
    background-position: 450px 0;
  }
}

