.icon__button {
  position: absolute;
  background-color: #ff6b01;

  top: 270px;
  right: -10px;
  width: 30px;
  height: 30px;
  border-radius: 10%;
  border: none;

  opacity: 0.7;

  display: flex;
  align-items: center;
  justify-content: center;
  transition: $typical-transition-opacity;

  &:hover {
    opacity: 1;
  }

  @include mq(tablet) {
    right: 3%;
    width: 40px;
    height: 40px;
  }
}


.gallery__image {
  margin-bottom: 8px;

  body.dark__theme & {
    box-shadow: 0 14px 28px rgba(255, 255, 255, 0.25),
      0 10px 10px rgba(255, 255, 255, 0.22);
  }
}